@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* helper classes */
.bottom-space {
  margin-bottom: $bottom-space;

  @include media('<mob') {
    margin-bottom: $bottom-space--mob;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.bottom-section {
  margin-bottom: $bottom-section;

  @include media('<desk-s') {
    margin-bottom: $bottom-section--lap;
  }

  @include media('<mob') {
    margin-bottom: $bottom-section--mob;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.mark {
  color: $mark-color;
}

.align-center {
  text-align: center;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.push-auto {
  margin-left: auto;
  margin-right: auto;
}

.nobr {
  white-space: nowrap;
}

.hide {
  display: none !important;
}

.hide-text {
  @include hide-text;
}

.desktop-only {
  @include media("<tab") {
    display: none;
  }
}

.devices-only {
  @include media(">=tab") {
    display: none;
  }
}

.mobile-only {
  @include media(">=mob") {
    display: none;
  }
}

.clearfix {
  @include clear;
  margin: 0;
}

/* ==================================== */
/* ======= NON CONTENT ELEMENTS ======= */
/* ==================================== */
/* fancybox */
.fancybox-bg {
  background-color: $bg-color;
}

.fancybox-button {
  background-color: $base-color;
  color: $text-color-alt;

  &[disabled], &[disabled]:hover {
    color: $text-color-alt;

    &,
    .fancybox-show-nav .fancybox-navigation & {
      opacity: 0.5;
    }
  }
}

/* fancybox end */

/* breadcrumbs */
.breadcrumbs {
  color: $text-color-muted;
  margin-bottom: $bottom-space;

  &:last-child {
    margin-bottom: 0;
  }
}

.breadcrumbs__list {
  padding: 0;
  margin: 0;
  list-style: none;
  cursor: default;
}

.breadcrumbs__item {
  display: inline;
  padding-right: 0.6rem;
  margin: 0;

  &::after {
    display: inline;
    content: "";
    padding-left: 0.7em;
  }

  &:last-child {
    &::after {
      content: none;
    }
  }
}

/* breadcrumbs end */

/* pagination */
// pagination
$pagination-hspace: 1.2rem;
$pagination-vspace: 0.4rem;
$pagination-font-size: 1.6rem;
$pagination-height: 4rem;
$pagination-width: $pagination-height;
$pagination-border-width: 0;

.pagination {
  @include font-bold;
  @include clear-spacing;
  color: inherit;
  text-align: center;
  cursor: default;
  margin: 0 auto #{$bottom-section};
  @include clear;

  // <1600px
  @include media("<desk-s") {
    margin-bottom: $bottom-section--lap;
  }

  @include media("<mob") {
    position: relative;
    margin-bottom: $bottom-section--mob;
    padding-bottom: $pagination-height + $pagination-vspace * 2;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.pg-left {
  float: left;

  @include media("<mob") {
    float: none;
    position: absolute;
    bottom: 0;
    right: 50%;
  }
}

.pg-right {
  float: right;

  @include media("<mob") {
    float: none;
    position: absolute;
    bottom: 0;
    left: 50%;
  }

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -0.1rem;
    width: 0;
    height: 2rem;
    margin-top: -1rem;
    border-left: 0.2rem solid;

    display: none;

    @include media("<mob") {
      display: block;
    }
  }
}

.pg-first-arr,
.pg-last-arr,
.pg-prev-arr,
.pg-next-arr {
  //display: none
  position: absolute;
  top: 50%;
  left: 0;
  height: 0.2rem;
  width: 1.2rem;
  margin-top: -0.1rem;
  letter-spacing: 0;
  border-top: 0.2rem solid $text-color;
  transition: border-color $animation-speed;

  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    $size: 0.8rem;
    height: $size;
    width: $size;
    margin-top: -0.1rem;
    border: 0 solid $text-color;
    backface-visibility: hidden;
    transition: border-color $animation-speed;
  }
}

.pg-first-arr,
.pg-prev-arr {
  &::before,
  &::after {
    transform: rotate(-45deg);
    transform-origin: left top;
    left: 0;
    margin-left: -0.1rem;
    border-width: 0.2rem 0 0 0.2rem;
  }
}

.pg-last-arr,
.pg-next-arr {
  left: auto;
  right: 0;

  &::before,
  &::after {
    right: 0;
    margin-right: -0.1rem;
    transform-origin: top right;
    transform: rotate(45deg);
    border-width: 0.2rem 0.2rem 0 0;
  }
}

.pg-first-arr,
.pg-last-arr {
  width: 1.8rem;
}

.pg-first-arr {
  &::after {
    margin-left: 0.6rem;
  }
}

.pg-last-arr {
  &::after {
    margin-right: 0.6rem;
  }
}

.pg-prev-arr,
.pg-next-arr {
  &::after {
    display: none;
  }
}

.pg-first,
.pg-last,
.pg-prev,
.pg-next {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  @if $pagination-border-width != 0 {
    margin: 0 0 0 -#{$pagination-border-width};
  } @else {
    margin: #{$pagination-vspace} #{$pagination-hspace};
  }
  color: inherit;
  text-decoration: none;
  transition: color $animation-speed;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  span {
    $space: 2rem;
    padding-left: $space;
    padding-right: $space;
  }

  &.disabled {
    opacity: 0.5;
    cursor: default;
    pointer-events: none;
  }

  .no-touchevents &:not(.disabled):hover {
    color: $base-color;

    [class*="-arr"] {
      &, &::before,
      &::after {
        border-color: $base-color;
      }
    }
  }
}

//&:not(.disabled):active
// margin-top: 1px

.pg-first,
.pg-last {
  display: none;

  span {
    $space: 2.6rem;
    padding-left: $space;
    padding-right: $space;
  }

  @include media("<mob") {
    display: none;
  }
}

.pg-first,
.pg-prev {
  span {
    padding-right: 0;
  }
}

.pg-last,
.pg-next {
  span {
    padding-left: 0;
  }
}

.pg-list {
  & {
    display: inline-block;
    vertical-align: middle;
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      margin: #{$pagination-vspace} 4px;
      padding: 0;
      @include media("<mob-s") {
        @include margin-horizontal(0);
      }

      &:before {
        content: none;
      }
    }

    li {
      display: inline-block;
      vertical-align: top;
      text-align: center;

      a,
      & > span {
        display: inline-block;
        vertical-align: top;
        color: inherit;
        background: transparent;
        min-width: $pagination-width;
        padding: 0 0.2rem;
        border-radius: 0.5rem;
      }

      a {
        text-decoration: none;
        cursor: pointer;
        transition: color $animation-speed;
        user-select: none;
        -webkit-tap-highlight-color: transparent;

        .no-touchevents &:hover {
          color: $base-color;
        }
      }

      &.active > span,
      &.active a,
      .no-touchevents &.active a:hover {
        color: white;
        background-color: $base-color;
        @if $pagination-border-width != 0 {
          border: {
            top-color: $base-color;
            bottom-color: $base-color;
          };
        }
      }
    }
  }
}

//    
.pg-first,
.pg-last,
.pg-prev,
.pg-next,
.pg-list a,
.pg-list li > span {
  height: $pagination-height;
  font-size: $pagination-font-size;
  line-height: $pagination-height - $pagination-border-width * 2;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  @if $pagination-border-width != 0 {
    border: #{$pagination-border-width} solid #{$border-color};
  }

  @include media("<mob-s") {
    font-size: 14px;
  }
}

.pg-list a,
.pg-list li > span {
  letter-spacing: normal;
}

/* pagination end */

.fp-section {
  will-change: transform;
}

//.main-section,
.about-section,
.m-news-section,
.media-section,
.partners-section {
  display: flex;
  align-items: center;
  min-height: 100%;
  padding-top: 18rem;
  padding-bottom: nth($layout-page-space, 3);

  @include media('<desk-s') {
    padding-top: $layout-page-top-space--lap;
    padding-bottom: nth($layout-page-space--lap, 3);
  }

  @include media('<lap-l') {
    padding-top: $layout-page-top-space--net;
    padding-bottom: nth($layout-page-space--net, 3);
  }

  @include media('<tab') {
    display: block;
    height: auto !important;
    min-height: 0;
    @include linear-gradient(to bottom, $bg-color-alt 0%, $bg-color 100%);
    padding-top: nth($layout-page-space--tab, 1);
    padding-bottom: nth($layout-page-space--tab, 3);
  }

  @include media('<mob') {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
}

.section-layout {
  flex: 1 0 100%;
  width: 100%;
  $space: #{nth($layout-page-space, 2)};
  padding-left: $space;
  padding-right: $space;

  @include media('<desk') {
    $space: nth($layout-page-space, 2);
    padding-left: $space;
    padding-right: $space;
  }

  @include media('<desk-s') {
    $space: nth($layout-page-space--lap, 2);
    padding-left: $space;
    padding-right: $space;
  }

  @include media('<lap-l') {
    $space: nth($layout-page-space--net, 2);
    padding-left: $space;
    padding-right: $space;
  }

  @include media('<tab') {
    $space: nth($layout-page-space--tab, 2);
    padding-left: $space;
    padding-right: $space;
  }

  @include media('<mob') {
    $space: nth($layout-page-space--mob, 2);
    padding-left: $space;
    padding-right: $space;
  }
}

/* gallery element */
.gallery-el {
  $figure-el: &;
  display: block;
  position: relative;
  padding: 0 3.1rem 4.2rem;

  @include media('<lap-l') {
    padding: 0 2rem 3rem;
  }

  @include media('<mob') {
    padding: 0 1.5rem 2.5rem;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 5rem;
    bottom: 0;
    width: 100%;
    z-index: -1;
    will-change: transform;
    transition: transform $animation-speed;
    transform-origin: top center;

    @include bg-news;
  }

  @at-root {
    a#{$figure-el} {
      text-decoration: none;
      color: inherit;
      user-select: none;
      -webkit-tap-highlight-color: transparent;
      outline: none;
    }
  }
}

.gallery-el__figure {
  position: relative;
  margin: 0 0 2rem;
  box-shadow: 0 2.3rem 4.2rem rgba(0, 0, 0, 0.20);
  will-change: transform;
  transition: transform $animation-speed;
  -webkit-overflow-scrolling: touch;

  @include media('<lap-l') {
    margin-bottom: 1.5rem;
  }

  &::before {
    @include img-mask;
  }

  &:last-child {
    margin-bottom: 0;
  }

  img {
    height: auto;
    max-width: none;
    width: 100%;
    margin: 0;
  }
}

.gallery-el__title {
  @include font-alt;
  font-size: 1.8rem;
  line-height: 2.4rem;
  color: $title-color;
  margin-bottom: 1.5rem;
  transition: color $animation-speed;

  @include media('<desk') {
    font-size: 1.6rem;
    line-height: 2.2rem;
  }

  @include media('<lap-l') {
    margin-bottom: 1.5rem;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

// Hover
.gallery-el {
  .no-touchevents &:hover {
    z-index: 1;

    &::before {
      $scale: 1.1, 1.1;
      transform: scale($scale);
      transform: scale3d($scale, 1);

      @include media('<lap-l') {
        $scale: 1.05, 1.05;
        transform: scale($scale);
        transform: scale3d($scale, 1);
      }
    }

    .gallery-el__title {
      color: $highlight-color;
    }

    .gallery-el__figure {
      $translate: 0, -1rem;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }
  }
}

/* gallery element end */

/* gallery */
.gallery {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 0 -1.2rem $bottom-section;

  @include media('<desk-s') {
    margin: 0 -1.2rem $bottom-section--lap;
  }

  @include media('<mob') {
    margin: 0 -0.6rem $bottom-section--mob;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.gallery__el {
  $width: 33.33%;
  flex: 0 1 $width;
  max-width: $width;
  padding: 0 1.2rem 4rem;

  @include media('<tab-s') {
    $width: 50%;
    flex-basis: $width;
    max-width: $width;
  }

  @include media('<mob') {
    padding: 0 0.6rem 3rem;
  }

  @include media('<mob-xxs') {
    $width: 100%;
    flex-basis: $width;
    max-width: $width;
  }
}

/* gallery end */

/* ==================================== */
/* ============== HEADER ============== */
/* ==================================== */
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  min-width: $layout-page-min-width;
  z-index: 50;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 120%;
    width: 100%;
    pointer-events: none;
    @include linear-gradient(180deg, $bg-color 0%, rgba($bg-color, 0.7) 60%, rgba($bg-color, 0) 100%);
  }
}

.header-layout {
  max-width: $layout-page-max-width;
  //box-sizing: content-box;
  @include push-auto;
  padding: 0 3rem 0 #{nth($layout-page-space, 2)};

  @include media('<desk') {
    padding-left: nth($layout-page-space--desk, 2);
  }

  @include media('<desk-s') {
    padding-left: nth($layout-page-space--lap, 2);
  }

  @include media('<lap-l') {
    padding-left: nth($layout-page-space--net, 2);
    padding-right: 2rem;
  }

  @include media('<tab') {
    padding-left: nth($layout-page-space--tab, 2);
  }

  @include media('<mob') {
    padding-left: nth($layout-page-space--mob, 2);
  }
}

.header-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.hc-right {
  position: relative;
  display: flex;
  align-items: center;
  min-height: $nav-height;

  @include media('<mob') {
    min-height: $nav-height--mob;
  }
}

/* logo */
.logo {
  flex-shrink: 0;
  position: relative;
  overflow: hidden;
  height: 14.2rem;
  width: 16.5rem;
  margin: 0 2rem 0 0;
  //@include linear-gradient(to bottom, rgba($base-color, 1) 0%, rgba($base-color, 0) 100%);
  background: linear-gradient(to bottom, rgba($base-color, 1) 0%, rgba($base-color, 0) 100%);
  background-image: -webkit-linear-gradient(to bottom, rgba($base-color, 1) 0%, rgba($base-color, 0) 100%);
  //background-size: 12.2rem;
  text-indent: -999rem;
  z-index: 5;
  transition: all $animation-speed;

  @include media('<desk-s') {
    width: 12rem;
    height: 10.4rem;
  }

  @include media('<mob') {
    height: $nav-height--mob;
    width: $nav-height--mob;
  }

  &::before,
  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    pointer-events: none;
    background-repeat: no-repeat;
    background-position: 50%;
    transition: opacity $animation-speed;
  }

  &::before {
    background-image: url(../../img/logo.svg);
    background-size: 12.2rem;

    @include media('<desk-s') {
      background-size: 9rem;
    }

    @include media('<mob') {
      background-image: url(../../img/logo-mob.svg);
      background-size: 3.2rem;
    }
  }

  &::after {
    background-position: 50% 45%;
    background-image: url(../../img/logo-horizontal.svg);
    background-size: 13.8rem;
    opacity: 0;

    @include media('<desk-s') {
      background-size: 10rem;
    }
  }

  a, & > span {
    position: absolute;
    display: block;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    font-size: 0;
    line-height: 0;
  }

  //     
  //   
  body:not(.home-page) & {
    .page-scrolled & {
      height: $nav-height;

      @include media('<mob') {
        height: $nav-height--mob;
      }

      &::before {
        opacity: 0;

        @include media('<mob') {
          opacity: 1;
        }
      }

      &::after {
        opacity: 1;

        @include media('<mob') {
          opacity: 0;
        }
      }
    }
  }
}

/* logo end */

/* lang */
.ms-drop__drop-js {
  opacity: 0;
  visibility: hidden;
  transition: opacity $animation-speed;

  .is-open & {
    opacity: 1;
    visibility: visible;
  }
}

.lang {
  position: relative;
  width: 6rem;
  @include font-bold;
  font-size: 2rem;
  line-height: 2rem;
  letter-spacing: 0.023em;
  text-align: center;
  margin-left: 4rem;

  @include media('<lap-s') {
    margin-left: 2rem;
  }

  a {
    position: relative;
    display: inline-block;
    vertical-align: top;
    padding: 0.3rem 0.5rem;
    color: white;
    text-decoration: none;
    transition: color $animation-speed;

    &::before {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      left: 0.4rem;
      width: calc(100% - 0.8rem);
      height: 0.2rem;
      background-color: currentColor;

      transform-origin: 100% 100%;
      $scale: 0, 1;
      transform: scale($scale);
      transform: scale3d($scale, 1);

      // transition
      backface-visibility: hidden;
      $prop: transform, opacity;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed;
        timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
      }
    }

    // Hover
    .no-touchevents &:hover {
      &::before {
        transform-origin: 0 100%;

        $scale: 1, 1;
        transform: scale($scale);
        transform: scale3d($scale, 1);
      }
    }
  }

  ul {
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    margin: 0;
    padding: 1.5rem 0;

    &::before {
      $intend: 1rem -0.5rem;
      content: "";
      display: block;
      position: absolute;
      top: #{nth($intend, 1)};
      left: #{nth($intend, 2)};
      height: calc(100% - #{nth($intend, 1)} * 2);
      width: calc(100% - #{nth($intend, 2)} * 2);
      @include bg-news;
    }

    li {
      margin-top: 0.5rem;

      &:first-child {
        margin-top: 0;
      }
    }

    a {
      &::before {
        content: none;
      }
    }

    .no-touchevents & a:hover {
      color: $base-color;
    }

    .active-item {
      a, .no-touchevents & a:hover {
        color: $text-color-muted;
        cursor: default;
      }
    }
  }
}

/* lang end */

/* search */
.h-search {
  position: relative;
  display: inline-block;
  vertical-align: top;
  $size: 1.6rem;
  height: $size;
  width: $size;
  text-indent: -999rem;
  overflow: hidden;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: contain;
  background-image: url(data:image/svg+xml;base64,);

  margin-left: 3.3rem;

  @include media('<desk-s') {
    margin-left: 1.5rem;
  }
}

/* search end */

/* contact popup */
// Contact opener
.h-call {
  position: relative;
  display: inline-block;
  vertical-align: top;
  $size: 2.4rem;
  height: $size;
  width: $size;
  text-indent: -999rem;
  overflow: hidden;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: contain;
  background-image: url(data:image/svg+xml;base64,);

  margin-left: 1.6rem;
}

// Social


/* social */
.contact-popup__social {
  margin: 0 0 3rem;

  &:last-child {
    margin-bottom: 0;
  }
}

.contact-popup__social-list {
  margin: 0 -0.6rem -0.6rem;
  cursor: default;
}

.contact-popup__social-item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  $size: 2rem;
  height: $size;
  width: $size;
  margin: 0 0.6rem 0.6rem;
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-align: left;
  color: $highlight-color;
  text-decoration: none;
  cursor: pointer;
  transition: color $animation-speed;

  .no-touchevents &:hover {
    color: $text-color-muted;
  }

  & > span {
    @include hide-text;
  }

  svg {
    $size: 2rem;
    $int: round-float($size / 2, 1);
    display: inline-block;
    vertical-align: top;
    position: absolute;
    top: 50%;
    left: 50%;
    height: $size;
    width: $size;
    margin: -#{$int} 0 0 -#{$int};
  }

  path {
    fill: currentColor;
  }
}

/* social end */

// Contact popup
.contact-popup {
  position: absolute;
  right: 0;
  top: 100%;
  //padding: 2rem 0 0;
  //width: 56rem;
  width: 30rem;
  cursor: default;
  // transition
  $prop: transform, opacity, visibility;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out;
  }
  // Hide
  opacity: 0;
  visibility: hidden;
  $translate: 0, 2rem;
  transform: translate($translate);
  transform: translate3d($translate, 0);

  &.is-open {
    opacity: 1;
    visibility: visible;
    $translate: 0, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
  }

  @include media('<mob') {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    padding: 2rem #{nth($layout-page-space--mob, 2)};
    z-index: 50;
  }
}

.contact-popup__holder {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 1.4rem;
  background-color: $bg-color-alt;
  box-shadow: 0 0.2rem 3.3rem rgba(0, 0, 0, 0.5);
  border-radius: 1.2rem;

  @include media('<mob-s') {
    display: block;
  }
}

.contact-popup__box {
  $width: 100%;
  flex: 0 1 $width;
  max-width: $width;
  padding: 1rem;
  box-sizing: border-box;

  @include media('<mob-s') {
    max-width: none;
  }
}

.contact-popup__list {
  font-size: 1.6rem;
  line-height: 2.0rem;
  color: white;
}

.contact-popup__list-item  {
  margin-bottom: 0.5rem;

  a[href^="tel:"] {
    color: inherit;
    text-decoration: none;
    transition: color $animation-speed;

    .no-touchevents &:hover {
      color: $highlight-color;
    }
  }
}

.contact-popup__note {
  color: $text-color-light;
  font-size: 1.2rem;
  line-height: 1.6rem;
}

.contact-popup-close {
  position: absolute;
  right: 1rem;
  top: 1rem;
  display: block;
  $size: 2rem;
  height: $size;
  width: $size;
  cursor: pointer;
  overflow: hidden;
  text-indent: -999rem;

  &::before {
    content: "";
    display: block;
    $size: 1.4rem;
    height: $size;
    width: $size;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-position: 50%;
    background-image: url(data:image/svg+xml;base64,);
    background-size: contain;
  }
}

/* contact popup end */

/* ==================================== */
/* ============ NAVIGATION ============ */
/* ==================================== */
.nav-wrap {
  position: relative;
  //   
  z-index: 50;

  // ------  -----------------------------------------
  @include media("<tab") {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 48rem;
    max-width: 100%;
    background-color: rgba($base-color, 0.9);
    z-index: 999;
    transition: {
      property: opacity, visibility, transform;
      duration: $animation-speed;
    };

    // hide
    opacity: 0;
    $translate: -100%, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
  }

  @include media('<tab-s') {
    width: 32rem;
  }

  @include media('<mob') {
    width: 100%;
  }

  &.is-open {
    // ------  -----------------------------------------
    @include media("<tab") {
      opacity: 1;
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }
  }
}

// Overlay
.nav-overlay {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(#000, 0.8);
  z-index: 998;
  transition: opacity $animation-speed;

  opacity: 0;
  pointer-events: none;

  display: none;

  // ------  -----------------------------------------
  @include media("<tab") {
    display: block;
  }
}

.nav-wrap.is-open ~ .nav-overlay {
  opacity: 1;
  pointer-events: auto;
}

// Navigation opener
.nav-opener {
  display: block;
  position: relative;
  $size: 4rem;
  height: $size;
  width: $size;
  font-size: 0;
  line-height: 0;
  cursor: pointer;

  display: none;

  &::before,
  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-position: 50%;
  }

  &::before {
    height: 1.7rem;
    width: 2.6rem;
    background-image: url(data:image/svg+xml;base64,);
  }

  &::after {
    height: 2.5rem;
    width: 2.7rem;
    background-image: url(data:image/svg+xml;base64,);
    display: none;
  }

  @include media('<tab') {
    display: block;
  }

  &.is-open {
    &::before {
      display: none;
    }

    &::after {
      display: block;
    }
  }
}

// Navigation header (for mobile)
.nav-header {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 0 0 1.5rem;
  border-bottom: 0.1rem solid rgba(white, 0.5);

  display: none;

  @include media('<tab') {
    display: block;
  }
}

// Navigation close button
.nav-close-btn {
  position: relative;
  display: block;
  $size: 4rem;
  height: $size;
  width: $size;
  margin-left: auto;
  overflow: hidden;
  text-indent: -999rem;

  &::before {
    content: "";
    display: block;
    $size: 1.8rem;
    height: $size;
    width: $size;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-position: 50%;
    background-image: url(data:image/svg+xml;base64,);
    background-size: contain;
  }
}

// Navigation
.nav {
  position: relative;

  // ------  -----------------------------------------
  @include media("<tab") {
    padding: 0;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
}

.nav__list {
  $nav-inner-space: 1.5rem 2rem;
  $nav-arrow-size: 4rem;
  $nav-item-space: 1em 1.5rem;
  $nav-item-indent: 0.4;

  display: flex;
  flex-wrap: wrap;
  cursor: default;
  color: $text-color-alt;
  font-size: 1.4rem;
  line-height: 1.8rem;

  @include media('<net') {
    font-size: 1.2rem;
    line-height: 1.8rem;
  }

  @include media('<tab') {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }

  //  ul
  &, & ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  // ------  -----------------------------------------
  @include media("<tab") {
    display: block;
  }

  //  li,
  //  li   (   )
  li {
    position: relative;

    // ------  -----------------------------------------
    @include media("<tab") {
      //  ,  
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
    }
  }

  //  "a"  "span"
  // "a"   "span"    
  a, li > span {
    //display: block;
    display: inline-block;
    vertical-align: top;
    position: relative;
    overflow: hidden;
    padding: 0.5rem;

    // ------  -----------------------------------------
    @include media("<tab") {
      padding: 0 1rem 0 0;
      overflow: visible;
    }
  }

  //  "a"
  a {
    color: inherit;
    text-decoration: none;

    // ------  -----------------------------------------
    @include media('<tab') {
      $width: calc(100% - #{$nav-arrow-size});
      max-width: $width;
    }
  }

  // ,    
  li.has-sub {
    & > a,
    & > span {
      padding-right: $nav-arrow-size;

      // ------  -----------------------------------------
      @include media("<tab") {
        padding-right: 1rem;

        //     ,
        //      "a"
        max-width: calc(100% - #{$nav-arrow-size});
      }
    }
  }

  //  ul (sub menu)
  ul {
    position: relative;
    flex-shrink: 0;
    width: 100%;
    flex-basis: 100%;

    display: none;

    // transition
    transition: opacity $animation-speed, visibility $animation-speed;

    // Important!
    //    
    @include media(">=tab") {
      display: block !important; // !     
      position: absolute;
      left: 0;
      top: 100%;
      width: 28rem;
      padding: 1.5rem 0;
      opacity: 0;
      visibility: hidden; //    ,     
      pointer-events: none; //      
    }

    &::before {
      $intend: 1rem -0.5rem;
      content: "";
      display: block;
      position: absolute;
      top: #{nth($intend, 1)};
      left: #{nth($intend, 2)};
      height: calc(100% - #{nth($intend, 1)} * 2);
      width: calc(100% - #{nth($intend, 2)} * 2);
      @include bg-news;
      z-index: -1;

      // ------  -----------------------------------------
      @include media("<tab") {
        content: none;
      }
    }

    //  
    //    
    ul {
      left: 100%;
      top: -0.5rem;
      text-transform: none;

      // ------  -----------------------------------------
      @include media("<tab") {
        left: auto;
        top: auto;
      }
    }

    //     
    li {
      margin: 0.5rem 0 0;

      // ------  -----------------------------------------
      @include media("<tab") {
        margin: 0;
        padding: #{nth($nav-item-space, 1)} 0 0 #{nth($nav-item-space, 2)};
      }

      &:first-child {
        margin-top: 0;
      }
    }

    a {
      display: block;
      transition: color $animation-speed;
    }

    // Hover
    li {
      //  .hover  
      &.hover,
      .no-js &:hover {
        & > a,
        & > span {
          color: $highlight-color;
        }
      }
    }

    // Current
    li.current {
      & > a,
      & > span {
        color: $highlight-color;

        @include media('<tab') {
          color: $text-color-muted;
        }
      }
    }

    // Current Hover
    li.current.hover {
      & > a {
        color: white;
      }
    }
  }

  //   
  li.current > ul {
    display: block;
  }

  //  .
  //  .hover  .
  //   ,   :hover.
  li.hover > ul,
  .no-js & li:hover > ul {
    //    
    @include media(">=tab") {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
      z-index: 5;
    }
  }

  //    .
  li > em {
    position: absolute;
    top: 50%;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    font-size: 0;
    line-height: 0;
    height: $nav-arrow-size;
    width: $nav-arrow-size;
    margin-top: -#{round-float($nav-arrow-size / 2, 1)};
    padding: 0;
    text-align: center;
    cursor: pointer;
    color: $text-color;
    transform: rotate(-90deg);
    transition: color $animation-speed;
    -webkit-tap-highlight-color: transparent;
    pointer-events: none;
    user-select: none;
    outline: none;

    &.jsNav__arrow_on {
      pointer-events: auto;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      $size: 1.2rem;
      height: $size;
      width: $size;
      margin: -#{round-float($size / 2, 1)} 0 0 -#{round-float($size / 2, 1)};
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: contain;
    }

    &:hover {
      &::after {
        @include linear-gradient(to bottom, lighten($base-color, 10%) 0%, $base-color 100%)
      }
    }

    @at-root {
      @include media("<tab") {
        li.is-open > em {
          transform: rotate(180deg);
        }
      }
    }

    // ------  -----------------------------------------
    @include media("<tab") {
      position: relative;
      top: auto;
      right: auto;
      margin: -1rem -1.5rem -1rem 0;
      transform: rotate(0);
      pointer-events: auto;
    }

    // Open, Hover
    @at-root {
      li.hover > em {
        transform: rotate(90deg);
      }

      @include media("<tab") {
        li.is-open > em {
          transform: rotate(180deg);
        }
      }
    }
  }

  // li  
  & > li {
    margin-right: 4rem;
    //@include font-bold;
    letter-spacing: 0.15em;
    text-transform: uppercase;

    @include media('<desk-s') {
      margin-right: 2rem;
    }

    @include media('<net') {
      margin-right: 1.3rem;
    }

    // ------  -----------------------------------------
    @include media("<tab") {
      padding: $nav-item-space;
      border-top: 1px solid rgba($text-color, 0.5);
      margin: 0;
    }

    &:first-child {
      // ------  -----------------------------------------
      @include media("<tab") {
        border-top: none;
      }
    }

    &:last-child {
      //margin-right: 0;

      // ------  -----------------------------------------
      @include media("<tab") {
        // no styles
      }
    }

    //  "a"  "span"  
    // "a"   "span"    
    & > a, & > span {
      &::before {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 0.4rem;
        width: calc(100% - 0.8rem);
        height: 0.2rem;
        //border-radius: 50%;
        background-color: currentColor;
        transform-origin: 100% 100%;
        $scale: 0, 1;
        transform: scale($scale);
        transform: scale3d($scale, 1);

        // transition
        backface-visibility: hidden;
        $prop: transform, opacity;
        will-change: $prop;
        transition: {
          property: $prop;
          duration: $animation-speed;
          //timing-function: ease-in-out;
          timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
        }

        // ------  -----------------------------------------
        @include media("<tab") {
          content: none;
        }
      }
    }

    //    
    & > em {
      //       ,  
      display: none;

      // ------  -----------------------------------------
      @include media("<tab") {
        display: block;
      }
    }

    // Hover
    //  .hover  
    &.hover,
    .no-js &:hover {
      & > a {
        &::before {
          transform-origin: 0 100%;

          $scale: 1, 1;
          transform: scale($scale);
          transform: scale3d($scale, 1);
        }
      }
    }

    //    
    // Current
    &.current {
      & > a,
      & > span {
        //@include font-bold;
        position: relative;
        z-index: 1;
        overflow: visible;
        cursor: default;

        // ------  -----------------------------------------
        @include media("<tab") {
          color: $text-color-muted;
        }

        &::after {
          content: "";
          position: absolute;
          top: -2.1rem;
          left: 0.5rem;
          right: 0.5rem;
          height: 0.5rem;
          background-color: $highlight-color;

          will-change: transform, opacity;
          transition: transform $animation-speed * 1.5;
          transform-origin: 3rem center;

          $scale: 0, 1;
          transform: scale($scale);
          transform: scale3d($scale, 1);

          z-index: -1;

          html.ready &,
          html.no-js & {
            opacity: 1;
            $scale: 1, 1;
            transform: scale($scale);
            transform: scale3d($scale, 1);
          }

          // ------  -----------------------------------------
          @include media("<tab") {
            content: none;
          }
        }
      }
    }
  }
}

/* ==================================== */
/* ============= FOOTER =============== */
/* ==================================== */
.footer {
  position: relative;
  overflow: hidden;
  border-top: 1px solid rgba($text-color-muted, 0.1);
  box-shadow: 0 -0.2rem 3.3rem rgba(0, 0, 0, 0.5);
  padding: 2rem 0;
  @include font-alt;
  font-size: 1.4rem;
  line-height: 1.48;
  color: $text-color-muted;
  background: $bg-color;

  @include media('<tab') {
    background: $bg-color-alt;
  }

  &.fp-section-js {
    height: auto !important;
    box-shadow: none;
  }
}

/* footer bottom */
.footer-bottom {
  position: relative;
}

.footer-bottom__holder {
  position: relative;
  display: flex;
  align-items: center;
  @include margin-horizontal(-#{round(nth($grid-space, 2) / 2)});

  @include media('<mob') {
    display: block;
    margin: 0;
  }
}

.footer-bottom__col {
  text-align: center;
  @include padding-horizontal(round(nth($grid-space, 2) / 2));
  $width: 33.33%;
  flex: 0 1 #{$width};
  max-width: $width;

  @include media('<mob') {
    max-width: none;
    margin: 2rem 0 0;
    padding: 0;
  }

  &:first-child {
    @include media(">=mob") {
      text-align: left;
    }

    @include media('<mob') {
      margin-top: 0;
    }
  }

  &:last-child {
    @include media(">=mob") {
      text-align: right;
    }
  }

  a {
    color: inherit;
    text-decoration: none;

    .no-touchevents &:hover {
      text-decoration: underline;
    }
  }
}

/* footer bottom end */

/* developer */
.developer {
  display: inline-block;
  line-height: 1;
  a {
    text-decoration: none;
    color: white;
  }
}

.developer__logo {
  display: block;
  position: relative;
  margin-bottom: 0.2rem;
  width: 13rem;
  font-size: 0;
  line-height: 0;

  &::before {
    content: "";
    display: block;
    @include ratio(247, 84);
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: url(../../img/logo-dev.png) no-repeat center center;
    background-size: contain;
    transition: filter $animation-speed;
    filter: grayscale(1);
  }
}

.developer__label {
  font-size: 1.3rem;
  line-height: 1.8rem;
  //letter-spacing: 0.1em;

  a {
    box-shadow: inset 0 0 0 currentColor;
    text-decoration: none;
    color: currentColor;
  }
}

// Hover
.developer {
  .developer__label {
    white-space: nowrap;

    a {
      text-decoration: none;
      transition: box-shadow $animation-speed;

      &:hover {
        text-decoration: none;
        box-shadow: inset 0 -1px 0 currentColor;
      }
    }
  }

  .developer__logo {
    &:hover {
      &::after {
        filter: grayscale(0);
      }
    }
  }
}

/* developer end */

/* ==================================== */
/* ============== MAIN ================ */
/* ==================================== */
.main {
  position: relative;
  padding-top: $layout-page-top-space;
  padding-bottom: nth($layout-page-space, 3);

  @include media('<desk') {
    padding-top: $layout-page-top-space--desk;
    padding-bottom: nth($layout-page-space--desk, 3);
  }

  @include media('<desk-s') {
    padding-top: $layout-page-top-space--lap;
    padding-bottom: nth($layout-page-space--lap, 3);
  }

  @include media('<lap-l') {
    padding-top: $layout-page-top-space--net;
    padding-bottom: nth($layout-page-space--net, 3);
  }

  @include media('<tab') {
    padding-top: $layout-page-top-space--tab;
    padding-bottom: nth($layout-page-space--tab, 3);
  }

  @include media('<mob') {
    padding-top: $layout-page-top-space--mob;
    padding-bottom: nth($layout-page-space--mob, 3);
  }
}

.h-main {
  overflow: hidden;

  & > .layout-page {
    max-width: none;
    box-sizing: border-box;
  }
}

.content {
  //background-color: rgba(whitesmoke, 0.8);
}

.section {
  margin-bottom: $bottom-section;

  @include media('<mob') {
    margin-bottom: $bottom-section--mob;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

/* page navigation */
.page-nav {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
  @include font-alt-bold;
  font-size: 1.4rem;
  line-height: 1.8rem;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: $text-color-alt;

  li {
    margin: 0 3.5rem 1.5rem 0;
  }

  a {
    text-decoration: none;
    color: inherit;
    transition: color $animation-speed;
  }

  li.current > a,
  li > span ,
  a.mPS2id-highlight {
    cursor: default;
    color: $highlight-color;
  }

  // Hover
  .no-touchevents & a:hover {
    color: $highlight-color;
  }
}

/* page navigation end */

/* caption */
.caption {
  display: flex;
  align-items: flex-start;
  cursor: default;

  @include media('<lap-l') {
    display: block;
  }

  h1, .h1 {
    margin-right: 4.5rem;
    margin-bottom: 3rem;

    @include media('<desk-s') {
      margin-right: 3rem;
    }
  }

  .page-nav {
    margin-top: 4.5rem;
    margin-bottom: 3rem;

    @include media("<desk") {
      margin-top: 2.7rem;
    }

    @include media('<desk-s') {
      margin-top: 2.7rem;
    }

    @include media('<lap-l') {
      margin-top: 0;
    }
  }
}

/* caption end */

/* words */
.wbg {
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  visibility: visible; // fix for safari
  z-index: -1;
  opacity: 0.5;
}

.wbg__list {
  position: relative;
  height: 100%;
  will-change: transform;
}

.wbg__item {
  position: absolute;
  left: 0;
  top: 0;
  @include font-alt-extra-bold;
  font-size: calc(30vw - 17rem);
  line-height: 1;
  color: #fff;
  white-space: nowrap;
  cursor: default;
  text-transform: uppercase;
  transform: rotate(90deg);
  transform-origin: left top;
  pointer-events: none;
  transition: margin 1.6s, opacity $animation-speed * 1.5;

  @include media('>=desk') {
    font-size: 47rem;
  }

  @include media('<lap-l') {
    font-size: calc(30vw - 10rem);
  }

  @include media('<tab') {
    font-size: calc(30vw - 6rem);
  }

  @include media('<mob') {
    font-size: calc(25vw);
  }

  &:nth-child(1) {
    margin-left: calc(1 * (1em - 0.22em));
    opacity: 0.07;
    margin-top: -5rem;
  }

  &:nth-child(2) {
    margin-left: calc(2 * (1em - 0.25em));
    opacity: 0.05;
    margin-top: -10vh;
  }

  &:nth-child(3) {
    margin-left: calc(3 * (1em - 0.26em));
    opacity: 0.03;
    margin-top: -20vh;
  }

  &:nth-child(4) {
    margin-left: calc(4 * (1em - 0.27em));
    opacity: 0.02;
    margin-top: -15vh;
  }

  & > span {
    display: block;
    position: relative;
    will-change: transform;
  }
}

/* words end */

/* menu */
// menu item
.menu-item {
  display: block;
  position: relative;
  text-decoration: none;
  color: inherit;
