  @include font-alt-extra-bold;
  font-size: $font-size-h1;
  line-height: 1;
  color: $title-color;
  text-transform: uppercase;
  margin: 0 3rem 1rem 0;
  text-decoration: none;

  // <1920px
  @include media("<desk") {
