  @include media("<lap-s") {
    font-size: 4.2rem;
  }

  // <1200px
  @include media("<net") {
    font-size: 4rem;
  }
}

.menu-item__view-more {
  display: inline-block;
  vertical-align: top;
  position: relative;
  @include font-alt-bold;
  font-size: 1.4rem;
  line-height: 2.0rem;
  white-space: nowrap;
  color: $text-color-alt;
  text-decoration: none;
  padding: 0 6.5rem 0 0;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  opacity: 0;
  $translate: 0, 1.5rem;
  transform: translate($translate);
  transform: translate3d($translate, 0);

  // transition
  $prop: transform, opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    delay: 0s;
    duration: $animation-speed;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    right: 0;
    top: 50%;
    height: 2.0rem;
    width: 3.6rem;
    margin-top: -1rem;
    pointer-events: none;
    background-repeat: no-repeat;
    background-image: url(data:image/svg+xml;base64,);
    background-size: contain;
    transition: transform $animation-speed;
  }

  // Hover
  .no-touchevents &:hover {
    &::before {
      $translate: -1rem, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }
  }

  // :active
  .no-touchevents &:active {
    &::before {
      transition-duration: $animation-speed / 2;

      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }
  }

  // Active
  .item_active & {
    transition-delay: 1s;

    opacity: 1;
    $translate: 0, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
  }
}

.menu-item__view-number {
  position: absolute;
  left: 0;
  top: 0;
  font-size: 2.3rem;
  line-height: 3.4rem;
  color: $text-color-alt;
