}

.menu-container {
  height: 100%;
}

.menu__list {
  display: flex;
  height: 100%;

  width: $section-content-width;
  max-width: calc(100% - #{$content-space-right});

  @include media('<lap-s') {
    max-width: calc(100% - #{$content-space-right--lap});
  }
}

.menu__item {
  //overflow: hidden;
  position: relative;
  height: 100%;
  $width: 25%;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: $width;
  max-width: $width;

  &::before {
    content: "";
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 0;
    height: 100%;
    border-left: 1px solid #252525;
    z-index: 2;
  }
}

/* menu end */

/* video cover */
.video-cover {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -2;
}

.video-cover__item {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  opacity: 0;

  transition: opacity $animation-speed;

  &.item_active {
    opacity: 1;
  }

  video, img {
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 0.4rem);
    height: 100%;
    @include object-fit(cover)
  }
}

.video-cover__overlay {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  background-color: rgba($bg-color, 0.5);
  background-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMzAwIiBoZWlnaHQ9IjMwMCIgdmlld0JveD0iMCAwIDMwMCAzMDAiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHJlY3Qgd2lkdGg9IjE1MCIgaGVpZ2h0PSIxNTAiIGZpbGw9ImJsYWNrIi8+PHJlY3QgeD0iMTUwIiB5PSIxNTAiIHdpZHRoPSIxNTAiIGhlaWdodD0iMTUwIiBmaWxsPSJibGFjayIvPjwvc3ZnPg==);
