/* menu for mobile */
.menu-mob {
  overflow: hidden;
  position: relative;
  font-size: 1.3vw; // 10px (1rem)   768px
  min-height: 60em;
  line-height: 1;
  padding: 14rem 2.5rem 4rem;
  display: none;

  @include media('<mob') {
    padding: 9rem 1.5rem 3rem;
  }

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none;
    overflow: hidden;
  }

  // <992px
  @include media("<tab") {
    display: block;
  }
}


// Backgrounds
.m-bg {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;

  .swiper-wrapper,
  .swiper-slide {
    height: 100%;
  }
}

.m-bg-el {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    //@include linear-gradient(to top, rgba($bg-color, 0.7) 0%, rgba($bg-color, 0.5) 50%, rgba($bg-color, 0.3) 100%);
    background-color: rgba($bg-color, 0.65);
  }
}

// Titles
.m-titles {
  position: relative;
  z-index: 5;
  margin-bottom: 3rem;

  // <640px
  @include media("<mob") {
    margin-bottom: 2rem;
  }

  &.swiper-container {
    overflow: visible;
  }

  .swiper-wrapper {
    counter-reset: section;
  }
}

.m-titles__item {
  padding-bottom: 2em;
  counter-increment: section;

  // <480px
  @include media("<mob-s") {
    padding-bottom: 4em;
  }
}

.m-titles-el {
  position: relative;
  display: inline-block;
  text-decoration: none;
  color: inherit;
  mix-blend-mode: multiply;

  & > em {
    display: block;
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 1.1em;
    @include font-alt;
    font-size: 2.8em;

    // <480px
    @include media("<mob-s") {
      top: 0.8em;
      font-size: 3.5em;
    }

    &::before {
      content: '0' counter(section);
      display: block;
      position: relative;
      $prop: transform, opacity;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: 0.5s;
        timing-function: cubic-bezier(.165,.84,.44,1);
      }
      $translate: 0, 102%;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }
  }

  & > strong {
    display: block;
    @include font-alt-extra-bold;
    font-size: 7em;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
    opacity: 0.5;
    $prop: transform, opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: 0.5s;
      timing-function: cubic-bezier(.165,.84,.44,1);
    }
  }

  & > span {
    display: block;
    padding-left: 2em;
    @include font-alt-extra-bold;
    font-size: 2.2em;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    text-decoration: none;
    color: $highlight-color;
    opacity: 0.55;
    $prop: transform, opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: 0.8s;
      timing-function: cubic-bezier(.165,.84,.44,1);
    }

    // <480px
    @include media("<mob-s") {
      font-size: 2.8em;
    }
  }

  // Active
  .swiper-slide-thumb-active & {
    & > em {
      &::before {
        $translate: 0, 0%;
        transform: translate($translate);
        transform: translate3d($translate, 0);
      }
    }

    & > strong {
      opacity: 1;
      $translate: 0.8em, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }

    & > span {
      opacity: 1;
      $translate: 4.5em, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }
  }
}

// Divider
.menu-mob-divider {
  position: relative;
  overflow: hidden;
  width: 35%;
  margin-bottom: 4rem;

  // <640px
  @include media("<mob") {
    margin-bottom: 3rem;
  }

  &::before {
    content: "";
    display: block;
    position: relative;
    height: 0.8em;
    width: 100%;
    background-color: $highlight-color;
    $prop: transform, opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: 0.5s;
      delay: 1s;
      timing-function: cubic-bezier(.165, .84, .44, 1);
    }
    $translate: -102%, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);

    .is-loaded & {
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }
  }

  z-index: 5;
}

// Description
.m-description {
  position: relative;

  &.swiper-container {
    overflow: visible;
  }

  &::before {
    content: "";
    position: absolute;
    left: -30%;
    top: -40%;
    height: 200%;
    width: 160%;
    @include linear-gradient(to top, rgba($bg-color, 0.8) 0%, rgba($bg-color, 0.7) 50%, rgba($bg-color, 0) 100%);
  }
}

.menu-mob-item {
  position: relative;
  max-width: 100%;
  width: 64rem;
}

.menu-mob-item__view-text {
  font-size: 2rem;
  line-height: 1.56;
  color: $text-color;
  margin-bottom: 2em;

  // <640px
  @include media("<mob") {
    font-size: 1.8rem;
  }

  // <480px
  @include media("<mob-s") {
    font-size: 1.6rem;
  }
}

/* menu for mobile end */

/* figure element */
.m-figure-el {
  $m-figure-el: &;
  display: block;
  position: relative;
  padding: 0 1.5rem 2rem;
  line-height: 1;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 5rem;
    bottom: 0;
    width: 100%;
    z-index: -1;
    will-change: transform;
    transition: transform $animation-speed;
    transform-origin: top center;

    @include bg-news;
  }

  @at-root {
    a#{$m-figure-el} {
      text-decoration: none;
      color: inherit;
      user-select: none;
      -webkit-tap-highlight-color: transparent;
      outline: none;
    }
  }
}

.m-figure-el__figure {
  position: relative;
  margin-bottom: 1.5rem;
  box-shadow: 0 2.3rem 4.2rem rgba(0, 0, 0, 0.20);
  will-change: transform;
  transition: transform $animation-speed;
  -webkit-overflow-scrolling: touch;

  &::before {
    @include img-mask;
  }

  &:last-child {
    margin-bottom: 0;
  }

  img {
    height: auto;
    max-width: none;
    width: 100%;
    margin: 0;
  }
}

.m-figure-el__title {
  @include font-alt;
  font-size: 1.5rem;
  line-height: 2.1rem;
  color: $title-color;
  margin-bottom: 1.5rem;
  transition: color $animation-speed;

  @include media('<lap') {
    font-size: 1.3rem;
    line-height: 1.9rem;
  }

  @include media('<mob-xxs') {
    font-size: inherit;
    line-height: inherit;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

// Hover
.m-figure-el {
  .no-touchevents &:hover {
    z-index: 1;

    &::before {
      $scale: 1.1, 1.1;
      transform: scale($scale);
      transform: scale3d($scale, 1);

      @include media('<lap-l') {
        $scale: 1.05, 1.05;
        transform: scale($scale);
        transform: scale3d($scale, 1);
      }
    }

    .m-figure-el__title {
      color: $highlight-color;
    }

    .m-figure-el__figure {
      $translate: 0, -1rem;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }
  }
}

/* figure element end */

/* figure list */
.m-figure-list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 0 -0.8rem -3rem;
}

.m-figure-list__el {
  $width: 33.33%;
  flex: 0 1 $width;
  max-width: $width;
  padding: 0 0.8rem 3rem;

  @include media('<mob-s') {
    $width: 50%;
    flex-basis: $width;
    max-width: $width;
  }

  @include media('<mob-xxs') {
    $width: 100%;
    flex-basis: $width;
    max-width: $width;
  }
}

/* figure list end */

/* about section */
.about-section {
  position: relative;
  font-size: 2.0rem;
  line-height: 3.2rem;
  color: $text-color-muted;

  @include media('<desk-s') {
    font-size: 1.6rem;
    line-height: 2.6rem;
