/* info widget */
$i-widget-space: 4rem;

// Weather
.weather-icon {
  line-height: 0;
  position: absolute;
  left: 38.6rem;
  top: 13.3rem;
  $size: 4rem;
  height: $size;
  width: $size;
  color: $text-color-alt;
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    display: inline-block;
    vertical-align: top;

    path, rect {
      fill: currentColor;
    }
  }
}

.weather-temp {
  position: absolute;
  left: 38.7rem;
  top: 17.2rem;
  font-size: 1.7rem;
  line-height: 2.5rem;
  color: $text-color-alt;
}

.weather-city {
  position: absolute;
  left: 38.7rem;
  top: 22.6rem;
  font-size: 1.4rem;
  line-height: 2.0rem;
  color: $text-color-muted;
}

.weather-time {
  position: absolute;
  left: 38.7rem;
  top: 24.6rem;
  font-size: 1.4rem;
  line-height: 2.0rem;
  color: $text-color-muted;
}

// Widget slider slide
.ws-el {
  position: relative;
  display: block;
  text-decoration: none;
  color: inherit;
  padding: 2.1rem 5.3rem 3.1rem 2.7rem;
  height: 28.6rem;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.ws-el__bg {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: #232323;
  box-shadow: 0 0.2rem 3.3rem rgba(0, 0, 0, 0.5);
  border-radius: 0.6rem;
}

.ws-el__figure {
  $intend: -4.2rem;
  width: calc(100% - #{$intend});
  position: relative;
  margin: 0 0 1.8rem #{$intend};
  background: #d8d8d8;
  box-shadow: 0.3rem 1rem 1.5rem rgba(0, 0, 0, 0.3);

  &::before {
    content: "";
    display: block;
    @include ratio(281, 131)
  }

  img {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    @include object-fit(cover);
  }
}

.ws-el__content {
  position: relative;
}

.ws-el__title {
  @include font-alt-bold;
  font-size: 2rem;
  line-height: 2.4rem;
  margin-bottom: 1rem;
  color: $title-color;

  &:last-child {
    margin-bottom: 0;
  }
}

.ws-el__text {
  font-size: 1.3rem;
  line-height: 1.9rem;
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0;
  }
}

// Widget slider
.i-widget-slider {
  padding: $i-widget-space;
  height: 100%;

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none;
    overflow: hidden;
  }

  .swiper-slide {
    margin: 0 auto;
    padding: 0 2rem 4.3rem 1.5rem;

    &:not(.swiper-slide-active) {
      pointer-events: none;
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    $size: 2.4rem;
    height: $size;
    width: $size;
    border-radius: $size;
    background-color: $highlight-color;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 1rem;
    transition: color $animation-speed, background-color $animation-speed;
    outline: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;

    // Hover
    .no-touchevents &:hover {
      background-color: $highlight-color--hover;
    }
  }

  .swiper-button-prev {
    display: none;
  }

  .swiper-button-next {
    top: auto;
    margin: 0;
    right: calc(7rem + #{$i-widget-space});
    bottom: calc(-1rem + #{$i-widget-space});
    background-image: url(data:image/svg+xml;base64,);
  }
}

// Widget background
.i-widget__bg {
  content: "";
  display: block;
  position: absolute;
  left: calc(#{$i-widget-space} + 3.5rem);
  top: calc(#{$i-widget-space} + 0.5rem);
  height: 32.4rem;
  width: 32.5rem;
  background: #232323;
  mix-blend-mode: normal;
  opacity: 0.6;
  box-shadow: 0 0.2rem 3.3rem rgba(0, 0, 0, 0.5);
  border-radius: 0.6rem;
}

// Open button
.btn-widget-opener {
  height: 16.4rem;
  width: 9.4rem;
  position: absolute;
  top: 12.6rem;
  right: 0;
  margin: 0 -3.1rem;
  cursor: pointer;
  background: #2e2e2e url(../../img/empty.png);
  border-radius: 1.6rem;
  z-index: 2;
  transition: background-color $animation-speed, opacity $animation-speed;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  .no-touchevents &:hover {
    background-color: #4e4e4e;
  }
}

// Close button
.btn-widget-close {
  position: absolute;
  right: calc(#{$i-widget-space} - 2rem);
  top: calc(#{$i-widget-space} - 1.5rem);
  $size: 2.4rem;
  height: $size;
  width: $size;
  border-radius: $size;
  color: white;
  cursor: pointer;
  @include font-bold;
  line-height: $size;
  font-size: 1.0rem;
  text-align: center;
  text-decoration: none;
  overflow: hidden;
  text-indent: -999rem;
  background-color: $highlight-color;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 1em;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  z-index: 5;

  transition: background-color $animation-speed;

  &:hover {
    background-color: $highlight-color--hover;
  }
}

// Widget
.i-widget {
  overflow: hidden;
  visibility: hidden;
  position: fixed;
  right: 0;
  top: 50%;
  height: calc(32.9rem + #{$i-widget-space} * 2);
  width: calc(36rem + #{$i-widget-space} * 2);
  margin-top: -20rem;
  z-index: 25;
  transition: transform $animation-speed;

  @include media('<lap-l') {
    margin-top: -23rem;
  }

  @include media('<lap-s') {
    margin-top: -20rem;
  }

  @include media('<tab') {
    display: none;
  }

  //.fp-viewing-section1 & {
  //  $translate: 0, -12.5rem;
  //  transform: translate($translate);
  //  transform: translate3d($translate, 0);
  //
  //  @include media('<lap-l') {
  //    $translate: 0, -2.5rem;
  //    transform: translate($translate);
  //    transform: translate3d($translate, 0);
  //  }
  //}

  & > * {
    visibility: visible;
  }

  .i-widget__bg,
  .i-widget-slider,
  .ws-el__figure,
  .weather-icon,
  .weather-temp,
  .weather-city,
  .weather-time,
  .btn-widget-close {
    will-change: transform, opacity;
    transition: all $animation-speed ease-in-out, background-color 0.2s;
  }

  .btn-widget-close {
    opacity: 0;
    pointer-events: none;
  }

  .i-widget__bg {
    border-radius: 3rem;
    opacity: 0;
    transform-origin: left center;
    $translate: 30rem, 0;
    $scale: 0.5, 0.5;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale3d($scale, 1);
  }

  .i-widget-slider {
    $translate: 43rem, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
    z-index: 2;
  }

  .ws-el__figure {
    opacity: 0;
    $translate: 40rem, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);
  }

  .weather-icon,
  .weather-temp,
  .weather-city,
  .weather-time {
    z-index: 3;
    pointer-events: none;
  }

  // Show full view of widget (active)
  &.widget_active {
    .btn-widget-close {
      transition-property: all, background-color;
      transition-delay: $animation-speed, 0s;
      opacity: 1;
      pointer-events: auto;
    }

    .btn-widget-opener {
      z-index: auto;
      opacity: 0;
    }

    .weather-icon{
      $translate: -20rem, -13.6rem;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }

    .weather-temp {
      $translate: -16.1rem, -16.6rem;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }

    .weather-city {
      $translate: -9.9rem, -21.7rem;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }

    .weather-time {
      $translate: -4.8rem, -23.7rem;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }

    .i-widget__bg {
      opacity: 0.6;
      border-radius: 0.6rem;
      $translate: 8.2rem, 0;
      $scale: 1, 1;
      transform: translate($translate) scale($scale);
      transform: translate3d($translate, 0) scale3d($scale, 1);
    }

    .i-widget-slider {
      $translate: 8.2rem, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }

    .ws-el__figure {
      opacity: 1;
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }
  }
}

/* info widget end */

/* to news */
.btn-to-news {
  position: absolute;
  right: calc(-1.2rem - 11rem);
  bottom: 11.7rem;
  width: 11rem;
  @include font-alt;
  font-size: 1.4rem;
  line-height: 1.6rem;
  text-transform: uppercase;
  text-decoration: none;
  color: $text-color-alt;
  transition: transform $animation-speed 0.6s;
  transform: translateX(0);
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  outline: none;

  @include media('<lap-l') {
    bottom: 6rem;
  }

  @include media('<tab') {
    display: none;
  }

  .fp-viewing-section1 & {
    transform: translateX(-100%);
  }

  & > span {
    display: block;
    padding: 1.6rem 1.3rem;
    border-radius: 1.4rem;
    //background-color: rgba(#2e2e2e, 0.8);
    background-color: rgba($highlight-color, 0.8);
    transition: background-color $animation-speed 0s;

    &::after {
      content: "";
      position: absolute;
      top: 100%;
      right: 3.2rem;
      $size: 2.2rem;
      height: $size;
      width: $size;
      margin: -0.8rem 0 0;
      border-radius: $size;
      //background-color: $highlight-color;
      background-color: white;
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: 1rem;
      background-image: url(data:image/svg+xml;base64,);
      transition: background-color $animation-speed;
    }
  }

  //  
  .fp-viewing-section1 &:not(:hover) > span {
    //animation: 2s tremor 1s ease-in-out infinite;
    animation: 10s tremor 1s ease-in-out infinite;
  }

  @mixin tremor () {
    $intend: 8%;
    $space: 2%;

    //0%, 25% {
    #{$space * 0}, #{$space * 5} {
      transform: translateY(0);
    }

    //5%, 15% {
    #{$space * 1}, #{$space * 3} {
      transform: translateY($intend);
    }

    //10%, 20% {
    #{$space * 2}, #{$space * 4} {
      transform: translateY(-#{$intend});
    }
  }

  @-webkit-keyframes tremor {
    @include tremor;
  }

  @keyframes tremor {
    @include tremor;
  }

  // Hover
  .no-touchevents &:hover {
