    min-height: 0;
    padding: 0;
  }

  @include media('<mob') {
    padding: 0;
  }
}

.btn-next-section {
  position: absolute;
  left: 50%;
  bottom: 3.5rem;
  z-index: 15;

  $size: 4rem;
  height: $size;
  width: $size;
  margin: 0 0 0 -#{round-float($size / 2, 1)};

  background-color: $highlight-color;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: 50%;
  border-radius: 0.2rem;

  font-size: 0;
  line-height: 0;

  transition: background-color $animation-speed;

  // <1440px
  @include media("<lap-l") {
    bottom: 2.5rem;
  }

  @include media('<tab') {
    display: none;
  }

  &:hover {
    background-color: $highlight-color--hover;
    $translate: 0, -1px;
    transform: translate($translate);
    transform: translate3d($translate, 0);
  }

  &:active {
    $translate: 0, 1px;
    transform: translate($translate);
    transform: translate3d($translate, 0);
  }
}

/* main section end */

/* multi news element */
.m-news-el {
  padding: 2rem 2.8rem 2rem 0;
  position: relative;
  display: flex;
  align-items: flex-start;
  color: $text-color;
  text-decoration: none;

  // <1280px
  @include media("<lap-s") {
    padding: 0 1.5rem 1.5rem 0;
  }

  // <992px
  @include media("<tab") {
    padding: 0;
  }

  @include media('<mob-s') {
    display: block;
    //padding: 0 0 2rem;
    padding: 0;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    //height: 18rem;
    left: 4rem;
    will-change: transform;
    transform-origin: left center;
    transition: transform $animation-speed;
    @include bg-news;

    @include media('<lap-l') {
      left: 2rem;
    }

    // <1280px
    @include media("<lap-s") {
      top: -1rem;
    }

    // <992px
    @include media("<tab") {
      left: 0;
      right: -1.5rem;
