  font-size: 1.4rem;
  line-height: 1.8rem;
  color: $title-color;
  letter-spacing: 0.15em;
  text-transform: uppercase;

  @include media('<mob') {
    margin-bottom: 1.5rem;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 1em;
    width: 1em;
    background-color: $base-color;
  }

  a {
    text-decoration: none;
    color: inherit;
    transition: color $animation-speed;

    .no-touchevents &:hover {
      color: $highlight-color;
    }
  }
}

/* similar news list end */

/* news slider */
.news-slider {
  $shadow-size: 3.5rem;
  $intend: 3.1rem;
  padding: 6rem #{$shadow-size + $intend} 3.5rem #{$shadow-size};
  margin: -6rem -#{$shadow-size + $intend} 0;
  //margin: -6rem -#{$shadow-size} #{$bottom-section - 3.5};

  // <1440px
  @include media("<lap-l") {
    $intend: 2rem;
    padding-right: #{$shadow-size + $intend};
    margin-left: -#{$shadow-size + $intend};
    margin-right: -#{$shadow-size + $intend};
  }

  // <1280px
  @include media("<lap-s") {
    $intend: 1.5rem;
    padding-right: #{$shadow-size + $intend};
    margin-left: -#{$shadow-size + $intend};
    margin-right: -#{$shadow-size + $intend};
  }

  @include media('<net') {
    margin-bottom: 0;
  }

  // <992px
  @include media("<tab") {
    padding-right: $shadow-size;
  }

  @include media('<tab-s') {
    padding-bottom: 4rem;
  }

  // <480px
  @include media("<mob-s") {
    padding-bottom: 6rem;
  }

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none;
    overflow: hidden;
  }

  .swiper-wrapper {
    align-items: flex-start;
  }

  .swiper-pagination {
    bottom: auto;
    left: auto;
    top: 0;
    right: #{nth($layout-page-space, 2) - 0.5};
    width: auto;
    padding: 0;

    @include media('<desk') {
      right: #{nth($layout-page-space--desk, 2) - 0.5};
    }

    @include media('<desk-s') {
      right: #{nth($layout-page-space--lap, 2) - 0.5};
    }

    @include media('<lap-l') {
      right: #{nth($layout-page-space--net, 2) - 0.5};
    }

    @include media('<tab-s') {
      top: auto;
      bottom: 0;
    }
  }
}

.news-slider__item {
  opacity: 0;
  pointer-events: none;
  transition: opacity $animation-speed;

  &.swiper-slide-visible {
    opacity: 1;
    pointer-events: auto;
  }
}

// Slider arrows
.news-slider__buttons {
  // <480px
  @include media("<mob-s") {
    position: relative;
    height: 0;
    margin-bottom: 2rem;
  }
}

.news-slider__button-prev,
.news-slider__button-next {
  position: absolute;
  top: 0.7rem;
  z-index: 10;

  // <1920px
  @include media("<desk") {
    top: -0.1rem;
  }

  // <1440px
  @include media("<lap-l") {
    top: -0.4rem;
  }

  // <1280px
  @include media("<lap-s") {
    top: 0;
  }

  // <640px
  @include media("<mob") {
    top: -0.5rem;
  }

  // <479px
  @include media("<mob-s") {
    top: auto;
    bottom: 0;
  }
}

.news-slider__button-prev {
  right: 7rem;

  // <1280px
  @include media("<lap-s") {
    right: 6rem;
  }
}

.news-slider__button-next {
  right: 0;
}

/* news slider end */


/* media section */
.media-section {
  position: relative;
  font-size: 2.0rem;
  line-height: 3.2rem;

  display: flex;
  //align-items: flex-start;
  align-items: center;

  @include media('<desk-s') {
    font-size: 1.6rem;
    line-height: 2.6rem;
  }

  h2 {
    @include font-alt-extra-bold;
    font-size: $font-size-h1;
    line-height: 1;
    color: $title-color;
    text-transform: uppercase;
    margin-bottom: 3rem;

    @include media('<desk') {
      font-size: $font-size-h1--desk;
      margin-bottom: 2.5rem;
    }

    @include media('<lap-l') {
      font-size: $font-size-h1--lap;
      margin-bottom: 2rem;
    }

    // <1280px
    @include media("<lap-s") {
      font-size: 4.2rem;
    }

    @include media('<mob') {
      font-size: $font-size-h1--mob;
    }
  }
}

.media-section__align {
  position: relative;
  z-index: 15;
  width: 130rem;
  max-width: calc(100% - 32rem);

  // <1200px
  @include media("<net") {
    width: auto;
    max-width: calc(100% - 6rem);
  }

  @include media('<tab') {
    max-width: none;
  }
}

/* media section end */

/* solutions element */
.sol-el {
  padding: 2rem 2.8rem 2.8rem 0;
  position: relative;
  display: flex;
  align-items: flex-start;

  color: $text-color;
  text-decoration: none;

  @include media('<lap-l') {
    padding-bottom: 2rem;
  }

  @include media('<mob-s') {
    display: block;
    padding: 0 0 2rem;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 3.4rem;
    will-change: transform;
    transition: transform $animation-speed;
    transform-origin: left center;
    @include bg-news;

    @include media('<lap-l') {
      left: 2rem;
    }

    @include media('<mob-s') {
      top: 2rem;
      left: -#{nth($layout-page-space--mob, 2)};
      right: -#{nth($layout-page-space--mob, 2)};
    }
  }
}

.sol-el__figure {
  position: relative;
  flex-grow: 0;
  flex-shrink: 1;
  margin: 0 2.8rem 0 0;
  $width: 23rem;
  flex-basis: $width;
  min-width: $width;
  max-width: $width;
  transition: transform $animation-speed;

  @include media('<mob') {
    margin-right: 2rem;
    $width: 18rem;
    flex-basis: $width;
    min-width: $width;
    max-width: $width;
  }

  @include media('<mob-s') {
    margin: 0 0 2rem;
    $width: 24rem;
    flex-basis: $width;
    min-width: $width;
    max-width: $width;
  }

  &::after {
    @include img-mask;
  }
}

.sol-el__content {
  position: relative;
  flex-grow: 0;
}

.sol-el__title {
  @include font-alt-extra-bold;
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: $title-color;
  text-transform: uppercase;
  margin-bottom: 2rem;
  transition: color $animation-speed;

  @include media('<desk-s') {
    font-size: 1.5rem;
    line-height: 2.0rem;
  }

  @include media('<lap-l') {
    margin-bottom: 1.2rem;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.sol-el__text {
  font-size: 1.5rem;
  line-height: 2.4rem;
  margin-bottom: 2rem;
  color: $text-color-muted;

  @include media('<desk-s') {
    font-size: 1.4rem;
    line-height: 2.0rem;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.sol-el__btn {
  display: inline-block;
  vertical-align: top;
  padding: 0.8rem 1.2rem;
  border-radius: 0.5rem;
  cursor: pointer;
  color: $text-color-alt;
  border: 1px solid rgba($text-color-alt, 0.3);

  transition: border-color $animation-speed;

  &:hover {
    border-color: $highlight-color;
  }
}

// States
.sol-el {
  // Hover
  .no-touchevents &:hover {
    z-index: 5;

    &::before {
      $translate: 0, 0;
      $scale: 1.1, 1.1;
      transform: translate($translate) scale($scale);
      transform: translate3d($translate, 0) scale3d($scale, 1);
    }

    .sol-el__figure {
      $translate: -2rem, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0);
    }

    .sol-el__title {
      color: $highlight-color;
    }
  }
}

/* solutions element end */

/* solutions list */
.solutions {
  position: relative;
  margin-bottom: round-float($bottom-section * 0.75, 1);

  @include media('<mob') {
    margin-bottom: round-float($bottom-section--mob * 0.75, 1);
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.solutions__list {
  margin: 0 -1.2rem -4rem;
  display: flex;
  flex-wrap: wrap;

  @include media('<mob') {
    margin: 0 -0.7rem -4rem;
  }
}

.solutions__item {
  padding: 0 1.2rem 4rem;
  flex-grow: 1;
  flex-shrink: 0;
  $width: 50%;
  flex-basis: $width;
  max-width: $width;

  @include media('<desk-s') {
    $width: 100%;
    flex-basis: $width;
    max-width: $width;
  }

  @include media('<mob') {
    padding: 0 0.7rem 4rem;
  }
}

/* solutions list end */

/* subscribe */
$subs-btn-width: 4.5rem;

.subscribe {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  z-index: 15;

  @include media('<mob') {
    justify-content: flex-start;
  }

  @include media('<mob-xs') {
    display: block;
  }
}

.subs-container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include media('<mob-s') {
    display: block;
  }
}

.sub-label {
  flex-grow: 1;
  font-size: 2rem;
  line-height: 2.4rem;
  margin: 0 1.3rem 0 0;

  @include media('<mob-s') {
    display: block;
    margin: 0 0 0.5em;
  }
}

.subs-input {
  $input: &;
  margin: 0;
  border-top: none;
  border-bottom: 0.2rem solid white;
  height: 4rem;
  padding: 1rem 2.2rem 0.8rem 2rem;
  font-size: 1.5rem;
  line-height: 2.0rem;
  color: white;
  background: none;

  @include media('<mob-s') {
    padding-left: 0;
  }

  @at-root {
    &, input#{&} {
      border-radius: 0;
    }
  }

  &.filled, &:focus, &:hover {
    background-color: transparent;
  }
}

.subs-btn {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: $subs-btn-width;
  padding: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 3.6rem;
  border: none;
  border-radius: 0 #{$form-def-border-radius} #{$form-def-border-radius} 0;
  font-size: 1.4rem;
  line-height: 1;
  text-align: center;
  color: black;
  cursor: pointer;
  outline: none;
  text-indent: -999rem;
  overflow: hidden;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  transition: background $animation-speed;

  .no-touchevents &:hover {
    //background-color: $highlight-color--hover;
    background-position: 100% 50%;
  }

  .no-touchevents &:active {
    transition-duration: 0.1s;
    background-position: 50% 50%;
  }
}

.subs-row {
  position: relative;
  width: 32rem;
  padding-right: 2.5rem;

  @include media('<mob') {
    width: 30rem;
  }

  @include media('<mob-xs') {
    width: 100%;
  }

  .subs-success {
    display: block;
    position: absolute;
    left: 0;
    bottom: 100%;
    width: 100%;
    padding: 0.5em 0;
    text-align: center;
    font-size: 2.0rem;
    line-height: 1;
    color: $text-color-alt;
    pointer-events: none;
    z-index: -1;

    $prop: transform, opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: 0.35s;
      timing-function: cubic-bezier(0.68, -0.55, 0.27, 1.55);
    }

    opacity: 0;
    $translate: 0, 2em;
    $scale: 0.9, 0.9;
    transform: translate($translate) scale($scale);
    transform: translate3d($translate, 0) scale($scale);

    @include media('<mob-s') {
      padding: 0.7em 0;
      font-size: 2.0rem;
      text-align: right;
    }

    .completed & {
      transition-delay: 0.35s;
      pointer-events: auto;
      opacity: 1;
      $translate: 0, 0;
      $scale: 1, 1;
      transform: translate($translate) scale($scale);
      transform: translate3d($translate, 0) scale($scale);
    }
  }

  .subs-error {
    position: absolute;
    top: 100%;
    left: 0;
    padding: 1rem 0 1rem #{$form-def-space-h};
    font-size: 1.4rem;
    line-height: 1.8rem;
    color: $form-error-color;

    display: none;
  }

  // Error
  input.subs-input.error ~ .subs-error {
    display: block;
  }

  // Completed
  &::before {
    //content: "";
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: $base-color;
    z-index: 5;
    border-radius: 0;

    $prop: border-radius, transform, opacity, visibility;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: 0.35s;
      timing-function: ease-in-out;
    }

    opacity: 0.8;
    visibility: hidden;
    transform-origin: right center;
    $scale: 0, 1;
    transform: scale($scale);
    transform: scale3d($scale, 1);

    .completed & {
      border-radius: $form-def-border-radius;
      opacity: 1;
      visibility: visible;
      $scale: 1, 1;
      transform: scale($scale);
      transform: scale3d($scale, 1);
    }
  }

  &::after {
    //content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    $size: 4rem;
    height: $size;
    width: $size;
    margin: -#{$size / 2} 0 0 -#{$size / 2};
    background-repeat: no-repeat;
    background-position: 50%;
    background-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMjQiIGhlaWdodD0iMjQiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyIgZmlsbC1ydWxlPSJldmVub2RkIiBjbGlwLXJ1bGU9ImV2ZW5vZGQiPjxwYXRoIGQ9Ik0yMSA2LjI4NWwtMTEuMTYgMTIuNzMzLTYuODQtNi4wMTggMS4zMTktMS40OSA1LjM0MSA0LjY4NiA5Ljg2NS0xMS4xOTYgMS40NzUgMS4yODV6Ii8+PC9zdmc+);
    background-size: 100%;
    pointer-events: none;
    z-index: 5;

    $prop: transform, opacity, visibility;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: 0.5s;
      timing-function: ease-in-out;
    }

    opacity: 0;
    visibility: hidden;
    $translate: 13.6rem, -0.3rem;
    $rotate: -90deg;
    $scale: 0.7, 0.7;
    transform: translate($translate) rotate($rotate) scale($scale);
    transform: translate3d($translate, 0) rotate($rotate) scale($scale);

    .completed & {
      opacity: 1;
      visibility: visible;
      $translate: 0, 0;
      $rotate: 0deg;
      $scale: 1, 1;
      transform: translate($translate) rotate($rotate) scale($scale);
      transform: translate3d($translate, 0) rotate($rotate) scale($scale);
    }
  }
}

/* subscribe end */

/* news article */
.news-page {
  h1 {
    margin-top: 0;
    text-transform: none;

    // >=992px
    @include media(">=tab") {
      font-size: $font-size-h1--lap;
      line-height: $line-height-h1--lap;
    }

    //@include media("<tab") {
    //  font-size: 4.8rem;
    //  line-height: 5.2rem;
    //}
    //
    //@include media("<mob") {
    //  font-size: 3.8rem;
    //  line-height: 4.4rem;
    //}
  }
}

// News meta
.news-meta {
  margin-bottom: $bottom-space;

  time {
    display: block;
    color: $text-color-muted;
  }
}

// News content
.news-content {
  margin-bottom: $bottom-section;

  @include media('<desk-s') {
    margin-bottom: $bottom-section--lap;
  }

  @include media('<mob') {
    margin-bottom: $bottom-section--mob;
  }

  .news-content h1 {
    font-size: 3.8rem;
    line-height: 4.7rem;

    @include media('<mob') {
      font-size: 2.8rem;
      line-height: 3.7rem;
    }
  }
}

// News source
.news-source {
  margin-bottom: $bottom-space;
  color: $text-color-alt;

  @include media('<mob') {
    margin-bottom: $bottom-space--mob;
  }

  a {
    text-decoration: underline;
    transition: color $animation-speed;
    color: $text-color-muted;

    .no-touchevents &:hover {
      color: $base-color;
    }
  }
}

// News thumb
.news-thumb {
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: $text-color-alt;
  background-repeat: no-repeat;
  max-width: 48rem;

  @include media('<mob') {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}

.news-thumb__date {
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: $text-color-muted;
  margin-bottom: 0.5rem;
}

.news-thumb__title {
  display: inline-block;
  vertical-align: top;
  transition: color $animation-speed;
}

.news-thumb_prev {
  text-align: left;
  padding-left: 6rem;
  background-position: 0 50%;
  background-image: url(data:image/svg+xml;base64,);

  @include media('<mob') {
    padding: 4rem 0 0;
    background-position: 0 0;
  }
}

.news-thumb_next {
  text-align: right;
  padding-right: 6rem;
  background-position: 100% 50%;
  background-image: url(data:image/svg+xml;base64,);

  @include media('<mob') {
    padding: 4rem 0 0;
    background-position: 100% 0;
  }
}

// States
.news-thumb {
  // Hover
  .no-touchevents &:hover {
    .news-thumb__title {
      color: $highlight-color;
    }
  }
}

// News thumbs
.news-thumbs {
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin: 0 -2rem $bottom-section;

  @include media('<desk-s') {
    margin-bottom: $bottom-section--lap;
  }

  @include media('<mob') {
    margin-bottom: $bottom-section--mob;
  }
}

.news-thumbs__item {
  $width: 50%;
  flex: 0 1 $width;
  max-width: 50%;
  padding: 0 2rem;
}

.news-thumbs__item_prev {
  text-align: left;
}

.news-thumbs__item_next {
  text-align: right;
}

/* news article end */

/* address */
.adr {
  margin-bottom: $bottom-section;

  @include media('<desk-s') {
    margin-bottom: $bottom-section--lap;
  }

  @include media('<mob') {
    margin-bottom: $bottom-section--mob;
  }

  address {
    display: block;
    margin-bottom: $bottom-space;
    @include font-normal;
    font-style: normal;
