/* address end */

/* contacts form */
.contacts-form {
  //padding: $grid-space;
  //margin: -#{nth($grid-space, 2)};
  border-radius: 0.5rem;
  //background-color: rgba($bg-color, 0.9);

  //@include media('<tab') {
  //  padding: 2rem #{nth($layout-page-space--tab, 2)};
  //  margin: -#{nth($layout-page-space--tab, 2)};
  //}
  //
