/* contacts form end */

/* contacts */
.contacts {
  position: relative;
  width: $contacts-layout;
  z-index: 1;

  @include media('<desk-s') {
    width: $contacts-layout--lap;
  }

  @include media('<lap-l') {
    width: $contacts-layout--net;
  }

  @include media('<tab') {
    width: auto;
    margin-bottom: $bottom-section--lap;
  }

  @include media('<mob') {
    margin-bottom: $bottom-section--mob;
  }

  h2 {
    position: relative;
    padding-left: 1.8em;
    text-transform: uppercase;

    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 0.2em;
      height: 1em;
      width: 1em;
      background-color: $base-color;
    }
  }
}

/* contacts end */

/* contacts map */
#map {
  position: fixed;
  top: 0;
  right: 0;
  left: calc(#{$contacts-layout} + #{nth($layout-page-space, 2)} * 2);
  //width: calc(100% - #{$contacts-layout} - #{nth($layout-page-space, 2)} * 2);
  height: calc(100% + 2rem);

  @include media(">desk") {
    left: calc(50% - #{$layout-page-max-width / 2} + #{$contacts-layout} + #{nth($layout-page-space, 2)} * 2);
  }

  @include media('<desk') {
    width: calc(100% - #{$contacts-layout} - #{nth($layout-page-space--desk, 2)} * 2);
  }

  @include media('<desk-s') {
    width: calc(100% - #{$contacts-layout--lap} - #{nth($layout-page-space--lap, 2)} * 2);
  }

  @include media('<lap-l') {
    width: calc(100% - #{$contacts-layout--net} - #{nth($layout-page-space--net, 2)} * 2);
  }

  @include media('<tab') {
    position: relative;
    top: auto;
    right: auto;
    width: auto;
    height: 40rem;
    margin: 0 -#{nth($layout-page-space--tab, 2)} -#{nth($layout-page-space--tab, 3)};
  }

  @include media('<mob') {
    height: 24rem;
    margin: 0 -#{nth($layout-page-space--mob, 2)} -#{nth($layout-page-space--mob, 3)};
  }
}

.mapboxgl-canvas {
  &:focus {
    outline: none;
  }
}

.mapboxgl-control-container {
  button {
    &:focus,
    &:active {
      outline: none;
      box-shadow: none;
    }
  }
}

.mapboxgl-ctrl-top-right {
  opacity: 0;
  transition: opacity $animation-speed;

  #map:hover & {
    opacity: 1;
  }

  .touchevents & {
    display: none;
  }

  top: $layout-page-top-space;

  @include media('<desk') {
    top: $layout-page-top-space--desk;
  }

  @include media('<desk-s') {
    top: $layout-page-top-space--lap;
  }

  @include media('<lap-l') {
    top: $layout-page-top-space--net;
  }

  @include media('<tab') {
    top: $layout-page-top-space--tab;
  }

  @include media('<mob') {
    top: $layout-page-top-space--mob;
  }
}

/* contacts map end */

/* button scroll to top */
.btn-to-top {
  position: fixed;
  right: -1.2rem;
  //bottom: 6.8rem;
  bottom: 12.5rem;
  z-index: 998;
  width: 9.5rem;
  padding: 1.6rem 1.3rem;
  @include font-alt;
  font-size: 1.4rem;
  line-height: 1.6rem;
  text-transform: uppercase;
  text-decoration: none;
  color: $text-color-alt;
  border-radius: 1.4rem;
  background-color: rgba(#2e2e2e, 0.8);
  transition: background-color $animation-speed 0s, transform $animation-speed 0.3s;
  $translate: 100%, 0;
  transform: translate($translate);
  transform: translate3d($translate, 0);
  box-shadow: 0 0.2rem 3.3rem rgba(0, 0, 0, 0.5);
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  outline: none;

  @include media('<mob') {
    width: 8.4rem;
    font-size: 1.2rem;
    line-height: 1.4rem;
    border-radius: 0.8rem;
    padding: 1.2rem 1rem;
    bottom: 2.5rem;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 100%;
    right: 2.5rem;
    $size: 2.2rem;
    height: $size;
    width: $size;
    margin: -0.8rem 0;
    border-radius: $size;
    background-color: $highlight-color;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 1rem;
    background-image: url(data:image/svg+xml;base64,);
    transition: transform 0.5s 0.3s, opacity 0.35s 0.3s;
    opacity: 0;
    transform: translateZ(0) rotate(-180deg);
  }

  span {
    display: block;
  }

  // Active
  &.btn-to-top--show {
    transition: background-color $animation-speed, transform $animation-speed 0s;
    $translate: 0, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0);

    &::after {
      transition: transform 0.5s 0.2s, opacity 0.35s 0.2s;
      opacity: 1;
      transform: translateZ(0) rotate(0);
    }
  }

  // Hover
  .no-touchevents &:hover {
    background-color: rgba(#4e4e4e, 0.8);
  }

  // Hover
  &:active {
    //margin: 1px -1px -1px;

    span {
      transform: scale(0.85);
    }
  }
}

/* button scroll to top end */